
import {defineComponent} from 'vue';

export default defineComponent({
  name: 'SendBtn',
  props: {
    is_send_disabled: {
      required: true
    },
    is_end: {
      required: true
    },
  },
  setup(props, context) {
    const send_main = () => {
      context.emit('send_main')
    }
    return {
      send_main
    }
  }
});
